import { useContext, useRef, useState } from 'react';
import { Helmet } from 'react-helmet';
import { Link } from 'react-router-dom';
import { titlePageGenerator } from '../../../utils';
import BannerPopup from '../../atoms/BannerPopup/BannerPopup';
import image from './loby-expo-hall.png';
import { AppContext } from '../../../context/AppContext';
import Menu3D from '../../organisms/Menu3D/Menu3D';
import Navigation3D from '../../organisms/Navigation3D/Navigation3D';
import useOnLoadImages from '../../../hooks/useOnLoadImages';
import TransitionLayer from '../../atoms/TransitionLayer/TransitionLayer';

export default function Lobby() {
  const [showBannerPopup, setShowBannerPopup] = useState(false);
  const ctx = useContext(AppContext);
  const wrapperRef = useRef<HTMLDivElement>(null);
  const imagesLoaded = useOnLoadImages(wrapperRef);

  return (
    <main>
      <Helmet>
        <title>{titlePageGenerator('Expo Hall')}</title>
      </Helmet>
      {showBannerPopup && <BannerPopup onClose={setShowBannerPopup} />}

      <div
        className="relative h-screen w-full overflow-hidden"
        ref={wrapperRef}
      >
        {ctx.showMenu3D && <Menu3D />}
        <Navigation3D href="/lobby" />

        <img
          src={image}
          className="absolute inset-0 -z-10 h-full w-full object-cover object-right"
          alt="expo hall"
        />
        <div className="flex h-full justify-end gap-[1.5vw] pr-[14vw]">
          <div className="h-screen w-[31vw]">
            <div className="h-[22vh] w-full flex-col items-center justify-center" />
            <div className="flex h-[13.6vh] w-full items-center justify-end">
              {/* <div className="h-full w-1/2 bg-primary/60">Main Banner</div> */}
            </div>
            <div className="h-[7.2vh] w-full flex-col items-center justify-center" />
            <div className="h-[20.7vh] w-full flex-col items-center justify-center">
              <div className="h-full w-[84%]">
                <button
                  type="button"
                  className="block h-full w-full"
                  onClick={() => setShowBannerPopup(true)}
                >
                  {' '}
                </button>
              </div>
            </div>
          </div>
          <div className="flex h-full w-full justify-end  md:w-[80vw] lg:w-[50vw]">
            <div className="mt-[19vh] flex h-[50vh] w-full flex-wrap justify-center gap-2 ">
              <Link
                to="/webinar-series"
                title="webinar series"
                className="h-[22vh] flex-1 flex-shrink-0 rounded-lg bg-white/0 text-center shadow-xl outline-white/0 hover:outline"
              />
              <Link
                to="/entrepreneurship-expo"
                title="entrepreneurship expo"
                className="h-[22vh] flex-1 flex-shrink-0 rounded-lg  bg-white/0 text-center shadow-xl outline-white/0 hover:outline"
              />
              <Link
                to="/expo-area"
                title="expo-area"
                className="mt-[20px] h-[22vh] w-full flex-shrink-0 rounded-lg  bg-white/0 text-center shadow-xl outline-white/0 hover:outline"
              />
            </div>
          </div>
        </div>
      </div>
      <TransitionLayer showTransition={imagesLoaded} />
    </main>
  );
}
